module.exports = "<template><u-article><h1>弹出层 Popper</h1>
<p>弹出层的设计思路请参阅<u-link href="/ui-design/popper">设计思路——弹出层系列</u-link>。</p>
<h2>示例</h2>
<h3>基本形式</h3>
<p>可以用一个 popper slot 来设置弹出元素，并用 default slot 来设置触发元素，这个触发元素通常也作为参照元素。由于 Vue 的限制，每个 slot 中只能有一个根节点。</p>
<u-code-example
    :show-code="false"
    :show-detail="false"
    file-path="src/components/u-old-popper.vue/README.md">
    <div><u-old-popper>
    <u-button>click</u-button>
    <div slot="popper">
        <u-block>popper</u-block>
    </div>
</u-old-popper></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-old-popper</span>&gt;</span>
