<h3>跟随鼠标</h3>
<p>将<code>'follow-cursor'</code>属性设置为<code>true</code>可以跟随鼠标。也可以传一个数字或对象调整位置偏移。</p>
<u-code-example
    :show-code="false"
    :show-detail="false"
    file-path="src/components/u-old-popper.vue/README.md">
    <div><u-linear-layout direction="vertical">
    <u-linear-layout>
        <u-old-popper trigger="hover" placement="top-start" follow-cursor>
