<h2>Popper API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>open.sync</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>弹出/隐藏状态</td>
</tr>
<tr>
<td>trigger</td>
<td>String</td>
<td><code>'click'</code></td>
<td>弹出层的触发方式。可选值：<code>'click'</code>, <code>'hover'</code>, <code>'right-click'</code>, <code>'double-click'</code>, <code>'manual'</code></td>
</tr>
<tr>
<td>placement</td>
<td>String</td>
<td><code>'bottom'</code></td>
<td>弹出层的弹出方向。可选值：<code>'top'</code>, <code>'bottom'</code>, <code>'left'</code>, <code>'right'</code>, <code>'top-start'</code>, <code>'top-end'</code>, <code>'bottom-start'</code>, <code>'bottom-end'</code>, <code>'left-start</code>',<code>'left-end'</code>, <code>'right-start'</code>, <code>'right-end'</code></td>
</tr>
<tr>
<td>offset</td>
<td>String</td>
<td><code>'0'</code></td>
<td>弹出层偏移，如：'10', '10px 10px', '10% 10%', 第一个值表示水平偏移，第二个值表示垂直位移, 默认单位是<code>px</code></td>
</tr>
<tr>
<td>follow-cursor</td>
<td>Boolean, Number, Object</td>
<td><code>false</code></td>
<td>是否跟随鼠标</td>
</tr>
<tr>
<td>hover-delay</td>
<td>Number</td>
<td><code>0</code></td>
<td>trigger 为 'hover' 时的延迟时间</td>
</tr>
<tr>
<td>hide-delay</td>
<td>Number</td>
<td><code>0</code></td>
<td>trigger 为 'hover' 时提示隐藏的延迟时间</td>
</tr>
<tr>
<td>append-to</td>
<td>String</td>
<td><code>'body'</code></td>
<td>指示添加到哪个元素</td>
</tr>
<tr>
<td>boundaries-element</td>
<td>Element, String</td>
<td><code>'window'</code></td>
<td>弹出层所在的边框元素，定义弹出层的边际，默认为第一个滚动的父级元素</td>
</tr>
<tr>
<td>escape-with-reference</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>当触发元素离开边际时，弹出层是否跟随离开或保留在边际元素内</td>
</tr>
<tr>
<td>options</td>
<td>Object</td>
<td><code>{ modifiers: {} }</code></td>
<td>补充 popper.js 的选项</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<p>触发节点，该 slot 只能插入一个节点。Popper 除了会给该节点绑定触发事件，不会对它做任何事情。</p>
<h4>popper</h4>
<p>弹出节点，该 slot 只能插入一个节点，作为弹出层的根节点。</p>
<h3>Events</h3>
<h4>@before-toggle</h4>
<p>弹出/隐藏前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>Boolean</td>
<td>弹出/隐藏状态</td>
</tr>
<tr>
<td>$event.preventDefault</td>
<td>Function</td>
<td>阻止弹出/隐藏流程</td>
</tr>
</tbody>
</table>
<h4>@toggle</h4>
<p>弹出/隐藏时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.open</td>
<td>Boolean</td>
<td>弹出/隐藏状态</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<h4>update</h4>
<p>更新 popper 实例</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody></tbody>
</table>
<h4>toggle</h4>
<p>切换弹出/隐藏状态</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>Boolean</td>
<td>弹出/隐藏状态</td>
</tr>
</tbody>
</table>
