<template>
<u-old-popup content="Popup" trigger="manual" :open.sync="open">
    <u-button @click="open = !open">{{ open ? '隐藏' : '弹出' }}</u-button>
</u-old-popup>
</template>

<script>
export default {
    data() {
        return {
            open: false,
        };
    },
};
</script>



