<template>
<u-old-popper trigger="manual" :open.sync="open">
    <u-button @click="open = !open">{{ open ? '隐藏' : '弹出' }}</u-button>
    <div slot="popper">
        <u-block>popper</u-block>
    </div>
</u-old-popper>
</template>

<script>
export default {
    data() {
        return {
            open: false,
        };
    },
};
</script>



