<h2>API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>String</td>
<td><code>'提示内容'</code></td>
<td>提示内容</td>
</tr>
<tr>
<td>open.sync</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>弹出/隐藏状态</td>
</tr>
<tr>
<td>trigger</td>
<td>String</td>
<td><code>'click'</code></td>
<td>弹出框的触发方式。可选值：<code>'click'</code>, <code>'hover'</code>, <code>'right-click'</code>, <code>'double-click'</code>, <code>'manual'</code></td>
</tr>
<tr>
<td>placement</td>
<td>String</td>
<td><code>'bottom'</code></td>
<td>弹出框的弹出方向。可选值：<code>'top'</code>, <code>'bottom'</code>, <code>'left'</code>, <code>'right'</code>, <code>'top-start'</code>, <code>'top-end'</code>, <code>'bottom-start'</code>, <code>'bottom-end'</code>, <code>'left-start</code>',<code>'left-end'</code>, <code>'right-start'</code>, <code>'right-end'</code></td>
</tr>
<tr>
<td>hideDelay</td>
<td>Number</td>
<td><code>0</code></td>
<td>提示内容消失延迟时间，单位是<code>'ms'</code></td>
</tr>
<tr>
<td>offset</td>
<td>String</td>
<td><code>'0'</code></td>
<td>弹出层偏移，如：'10', '10px 10px', '10% 10%', 第一个值表示水平偏移，第二个值表示垂直位移, 默认单位是<code>px</code></td>
</tr>
<tr>
<td>follow-cursor</td>
<td>Boolean, Number, Object</td>
<td><code>false</code></td>
<td>是否跟随鼠标</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>merge-borders</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>是否自动合并内外边框</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<p>触发节点，该 slot 只能插入一个节点。Tooltip 除了会给该节点绑定触发事件，不会对它做任何事情。</p>
<h4>popper</h4>
<p>自定义整个弹出层。</p>
<h4>head</h4>
<p>自定义头部。</p>
<h4>body</h4>
<p>自定义中部。</p>
<h4>foot</h4>
<p>自定义尾部。</p>
<h4>title</h4>
<p>自定义标题文本。</p>
<h4>content</h4>
<p>自定义内容文本。</p>
<h3>Events</h3>
<h4>@before-toggle</h4>
<p>弹出/隐藏前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>Boolean</td>
<td>弹出/隐藏状态</td>
</tr>
<tr>
<td>$event.preventDefault</td>
<td>Function</td>
<td>阻止弹出/隐藏流程</td>
</tr>
</tbody>
</table>
<h4>@toggle</h4>
<p>弹出/隐藏时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.open</td>
<td>Boolean</td>
<td>弹出/隐藏状态</td>
</tr>
</tbody>
</table>
</u-article></template>

